export default {
	data() {
		return {
			isLoading: true,
			imgUrl: this.imgUrl,
			modelName: '',
			storeCenterMsg: null,
			phone: null,
			isNull: false,
			shop_id: null
		}
	},
	onLoad() {
		this.getStoreMsg()
	},
	methods: {
		toShopTeam() {
			uni.navigateTo({
				url: '../myTeam/shopTeam?shop_name=' + this.storeCenterMsg.shop_name
			})
		},
		toCode() {
			console.log('生成二维码')
			uni.navigateTo({
				url: '../myTeam/codeShare'
			})
		},
		toShop() {
			uni.redirectTo({
				url: "../index/index"
			})
		},
		takePhone(phone) {
			this.phone = phone
			this.modelName = 'phoneModel'
		},
		closeModel() {
			this.modelName = ''
		},
		sureTakePhone() {
			uni.makePhoneCall({
				phoneNumber: this.phone
			});
			this.closeModel()
		},
		getStoreMsg() {
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/UserCenter/getStoreInfo",
				data: {
					token: uni.getStorageSync('token'),
					shop_id: uni.getStorageSync('shop_id')
				},
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.isLoading = false
					if (res.data.status == 0) {
						this.storeCenterMsg = res.data.data
						return
					}
					this.showToast()
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
	}
}
